<template>
    <div>
        <div class="loader" v-if="!showScore"></div>

        <div v-else>
            <h2>你的分數: <span class="text-danger">{{ score.score }}</span></h2>
            <p>{{ user.name }} - {{ user.group }}</p>
            <hr>
            <button class="btn btn-primary" @click="keepgoing">繼續</button>
            <hr>
            <div v-for="(s, index) in score.answer" 
                 v-html="decodeHtml(s, index)" class="text-left"></div>
        </div>

        <hr>
    </div>
</template>
<script>
    export default {
        props:['showScore', 'user', 'score'],
        methods: {
            decodeHtml: function (html, index) {
                var txt = document.createElement("textarea");
                txt.innerHTML = html.replace('&lt;p&gt;','&lt;p&gt;'+ (index+1) +'.&nbsp;');
                return txt.value;
            },
            keepgoing() {
                this.$emit('finish');
            }
        },
        created () {
            var self = this;
            setTimeout(function() {
                self.keepgoing();
            }, 15000);
        }
    }
</script>
<style>
.loader {
    border: 8px solid #f3f3f3; 
    border-top: 8px solid #3498db; 
    border-bottom: 8px solid #3498db; 
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 2s linear infinite;
    display: inline-block;
    margin-top: 20px;
}
ins{color: #5cb85c;}
del{color: #d9534f;}
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
</style>


