<template>
        <form action="" class="form-horizontal" id="myForm" autocomplete="off">
            <div class="form-group" v-if="!isReadOnly.isRead">
                <label for="group" class="col-sm-4 control-label">組別</label>
                <div class="col-sm-4">
                    <select name="group" id="group" 
                        class="form-control" 
                        v-model="groupClass"
                        :disable="isReadOnly.isRead">
                        <option v-for="group in groups" :value="group.group">{{group.group}}</option>
                    </select>
                </div>       
            </div>
            <div class="form-group">
                <label for="group" class="col-sm-4 control-label">小組</label>
                <div class="col-sm-4">
                    <select name="group" id="group" class="form-control" v-validate="'required'"
                        v-model="user.group"
                        v-if="!isReadOnly.isRead"
                        :disable="isReadOnly.isRead">
                        <option v-for="leader in leaders" :value="leader">{{leader}}</option>
                    </select>
                    <span class="text-danger" v-show="errors.has('group')">{{ errors.first('group') }}</span>
                    <p class="control-label user" v-if="isReadOnly.isRead">{{ user.group }}</p>
                </div>       
            </div>
            <div class="form-group">
                <label for="name" class="col-sm-4 control-label">姓名</label>
                <div class="col-sm-4">
                    <input type="text" class="form-control" id="name" name="name"
                        placeholder="請填寫姓名"       
                        v-model="user.name"
                        v-if="!isReadOnly.isRead"
                        :disable="isReadOnly.isRead"
                        v-validate="'required'">
                    <span class="text-danger" v-show="errors.has('name')">{{ errors.first('name') }}</span>
                    <p class="control-label user" v-if="isReadOnly.isRead">{{ user.name }}</p>
                </div>
            </div>
            <div class="form-group">
                <label for="mobile" class="col-sm-4 control-label">手機</label>
                <div class="col-sm-4">
                    <input type="phone" 
                        class="form-control" 
                        id="mobile" 
                        name="mobile"
                        placeholder="請填寫手機" 
                        v-model="user.mobile"
                        v-if="!isReadOnly.isRead"
                        :disabled="isReadOnly.isRead"
                        v-validate="'required|numeric|twPhone'">
                    <span class="text-danger" v-show="errors.has('mobile')">{{ errors.first('mobile') }}</span>
                    <p class="control-label user" v-if="isReadOnly.isRead">{{ user.mobile }}</p>
                </div>
            </div>
            <div class="form-group">
                <label for="mobile" class="col-sm-4 control-label">科目</label>
                <div class="col-sm-4">
                    <select name="eclass" id="eclass" v-model="user.eclass" 
                        class="form-control"
                        v-validate="'required'">
                        <option v-for="eclass in eclasses" :value="eclass">{{ eclass }}</option>
                    </select>
                    <span class="text-danger" v-show="errors.has('eclass')">{{ errors.first('eclass') }}</span>
                </div>
            </div>
            <button type="submit" class="btn btn-default" 
                @click.prevent="resetForm" 
                v-if="!isReadOnly.isRead">重填</button>
            <button type="submit" class="btn btn-default" @click.prevent="checkForm">開始測驗</button>
        </form>
    
</template>

<script>
    export default {
        data() {
            return {
                user: {
                    group: '',
                    name: '',
                    mobile: '',
                    eclass: 'C1 測驗卷'
                },
                groups: [ { group:'', list:[] } ],
                groupClass: ''
            }
        },
        props: ['eclasses', 'isReadOnly'],
        computed: {
            leaders() {
                var resault = this.groups.filter((e) => e.group === this.groupClass);
                return resault[0].list;   
            }
        },
        methods: {
            resetForm() {
                document.getElementById("myForm").reset();
            },
            checkForm() {
                this.$validator.validateAll().then(() => {
                    let classParam = '';
                    let formData = {};
                    formData.class = this.user.eclass

                    var userData = this.user; 
                    switch (this.user.eclass) {
                        case 'C1 測驗卷':
                            classParam = 'C1';
                            break;
                        case 'C2 一、二課測驗卷':
                            classParam = 'C212';
                            break;
                        case 'C2 三、四課測驗卷':
                            classParam = 'C234';
                            break;
                    }

                    userData.eclass = classParam;
                    formData.user = userData;
                    formData.type = 'paper';
                    this.$emit('finishForm', formData);
                }).catch((e) => {
                    alert('請確認各項資料是否無誤！');
                    return;
                });

            }
        },
        created () {
            if(this.isReadOnly.isRead == false){
                var self = this;
                $.get('Api/GetGroupItem.aspx', function(data){
                    var groupData = JSON.parse(data);
                    self.groups = groupData.DataInfo;
                    self.groupClass = groupData.DataInfo[0].group;
                });
            }else{
                this.user = this.isReadOnly.user;
            }
                      
        }
    }
</script>
<style scoped>
    .user{text-align: center;}
</style>




