<template>
    <div v-html="html"></div>
</template>

<script>
    export default {
        data() {
            return{
                question: this.data,
                html: '',
                optionString: this.option
            }
        },
        props: ['data','option','index','q_id'],
        created() {
            let html = '';
            for(var key in this.question){
                var value = this.question[key];
                if( typeof value !== 'number'){
                    if( value == "true" ){
                        value = ' <select required>'+this.optionString+'</select> ';
                    }
                    html += value;
                }         
            }
            this.html = '<div class="panel panel-default question" quesID="'+ this.q_id +'">\
                                        <div class="panel-body text-left">' + this.index +'. '+
                                             html+
                                        '</div>\
                                    </div>';
                        
        }
    }
</script>


