<template>
  <div id="myApp">
    <h1 v-cloak>{{ testTitle }}</h1>
    <transition name="fade" mode="out-in">
      <app-form v-if="type === 'form'" 
        @finishForm="getData($event)" 
        key="form"         
        :eclasses="eclasses"
        :isReadOnly="isReadOnly"
      ></app-form>  
      <app-paper v-if="type === 'paper'" key="paper" @getData="getScore($event)" :user="user"></app-paper> 
      <app-score v-if="type === 'score'" :showScore="showScore" :user="user" :score="score" @finish="doneTest"></app-score>
      <app-finish v-if="type === 'finish'" 
        :text="finalText" 
        :showBtn="showContinueTestBtn"
        @resetFrom="resetForm"
      ></app-finish>
    </transition>
  </div>
</template>

<script>
import Form from './components/Form.vue';
import Paper from './components/Paper.vue';
import Score from './components/Score.vue';
import FinishPage from './components/finish.vue';

export default {
  name: 'app',
  data () {
    return {
      type: 'form', 
      user: {},
      showScore: false,
      score:{},
      testTitle: '線上測驗',
      finalText: '謝謝您的答題，上帝祝福您有美好的一天',
      eclasses: ['C1 測驗卷', 'C2 一、二課測驗卷', 'C2 三、四課測驗卷'],
      showContinueTestBtn: false,
      isReadOnly: {
        isRead: false,
        user: {}
      }
    }
  },
  methods: {
    doneTest(){
      this.type = 'finish';
    },
    getData(e) {
      this.type = e.type;
      this.user = e.user;
      this.testTitle = e.class;
    },
    getScore(d) {
      this.showScore = false;
      this.type = 'score';
      //Send exam resault to api.
      var self = this;
      $.ajax({
          type: "post",
          url: "/api/GetScore.aspx",
          dataType:'json',
          data: {
            answer: JSON.stringify(d)
          },
          success: function (response) {
              self.score = response.DataInfo[0];
              //顯示過或不過的訊息
              if(!response.IsPass){
                self.finalText = '謝謝您的答題，您的考試成績尚未通過，請再接再厲！';
              }else{
                self.finalText = '謝謝您的答題，上帝祝福您有美好的一天';
              }
              //我要繼續考試的題目選項及按鈕顯示
              if(response.EClassData.length != 0){
                self.eclasses = response.EClassData;
                self.showContinueTestBtn = true;
              }else{
                self.showContinueTestBtn = false;
              }
              self.showScore = true; 
          }
      })
      .fail(function() {
          alert( "伺服器似乎是出現了錯誤，請稍後再試！" );
          self.type = 'form';
      });
    },
    resetForm() {
      this.isReadOnly.isRead = true;
      this.isReadOnly.user = this.user;
      this.testTitle = '線上測驗';
      this.type = 'form';
    }
  },
  components: {
    appForm: Form,
    appPaper: Paper,
    appScore: Score,
    appFinish: FinishPage
  }
}
</script>

<style>
.fade-enter{opacity: 0;}
.fade-enter-active{transition: opacity 1s;}
.fade-leave{}
.fade-leave-active{
  transition: opacity .5s;
  opacity: 0;
}
</style>




