<template>
    <div>
        <h2>{{ text }}</h2>
        <button class="btn btn-primary" v-if="showBtn" @click="resetFrom">我要繼續考下一科</button>
    </div>
</template>

<script>
export default {
    props: ['text', 'showBtn'],
    methods: {
        resetFrom() {
            this.$emit('resetFrom');
        }
    }
}
</script>




