<template>
    <div>
        <p class="text-warning">答錯每格扣10分，全錯0分。</p>
        <p class="text-danger">測驗將於 {{ leftTime }} 分後自動提交算分數。</p>
        <hr>
        <img src="../assets/gears.svg" v-if="!canRender" style="margin-bottom: .5em;"><br>
        <p v-if="!canRender">題目讀取中...</p>
        <transition-group name="staggered-fade"
                            tag="div"
                            v-bind:css="false"
                            v-on:before-enter="beforeEnter"
                            v-on:enter="enter"
                            v-on:leave="leave">
            <app-question v-for="(q, index) in exam" :data="q" :option="htmlString" :index="index+1"                         v-if="canRender" key="question" :q_id="quesId(q)"></app-question>
        </transition-group>
        
        <button class="btn btn-primary" @click="sendExam" v-if="canRender">提交</button>
        <hr>
    </div>
</template>

<script>
    import Question from './Question.vue';

    let data_exam = {},data_ans = {};
    
    export default {
        data() {
            return {
                leftTime: 10,
                exam: {},
                answer: {},
                htmlString: '',
                isGetExam: false,
                isGetAnsItem: false,
                canRender: false,
                index: 0,
                time: ''
            }
        },
        props: ['user'],
        methods: {
            timing() {
                var self = this;
                this.time = setInterval(()=>{
                    self.leftTime -= 1;
                    if( self.leftTime == 0 ){
                        clearInterval(self.time); 
                        alert('很抱歉考試時間已經到囉！開始交卷！')                  
                        self.sendExam();
                    }
                },60000);
            },
            makeOptions() {
                if(this.isGetExam == true && this.isGetAnsItem == true) {
                    let html = '';
                    for(var key in this.answer){
                        var value = this.answer[key].ItemName;
                        html += '<option value="'+ value +'">'+ value +'</option>';             
                    }
                    this.htmlString = html;
                    this.canRender = true;
                    this.timing();
                }
            },
            quesId(data) {
                return data['ID'];
            },
            sendExam() {
                var arr = [],i = 0;
                $('.question').each(function(){
                    var _this = $(this);
                    var a = {};
                    a.id = _this.attr('quesID');
                    a.answer = _this.find('select').map(function(){
                        return this.value
                    }).get();     
                    arr[i] = a;
                    i++;
                });
                let data = {
                    user: [this.user],
                    exam: arr
                }
                this.$emit('getData', data);   
            },
            getExam() {
                var self = this;
                $.ajax({
                    type: "post",
                    url: "Api/GetExam.aspx",
                    dataType:'json',
                    data: { 
                        user: JSON.stringify(self.user) 
                    },
                    success: function (resault) {
                        if(resault.IsError == true){
                            swal({
                                    title: "錯誤！",
                                    text: resault.ErrorMessage,
                                    type: "error",
                                },
                            function(){
                                location.reload();
                            });       
                        }else{           
                            self.exam = resault.DataInfo;
                            self.isGetExam = true;
                            self.makeOptions();
                        }   
                          
                    }
                })
                .fail(function() {
                    swal({
                            title: "Oops...",
                            text: "伺服器似乎是出現了錯誤，請稍後再試！",
                            type: "error",
                        },
                    function(){
                        location.reload();
                    });
                });
   
                $.post('Api/GetAnsItem.aspx', { eclass: self.user.eclass }, function(resault) {
                    data_ans = JSON.parse(resault);
                    self.answer = data_ans.DataInfo;
                    self.isGetAnsItem = true;
                    self.makeOptions();
                });     
            },
            beforeEnter: function (el) {
                el.style.opacity = 0
            },
            enter: function (el, done) {
                var delay = this.index * 150;
                this.index += 1;

                setTimeout(function () {
                    $.Velocity(
                        el,
                        { opacity: 1 },
                        { complete: done }
                    )
                }, delay)
            },
            leave: function (el, done) {
                var delay = this.index * 150
                this.index -= 1;

                setTimeout(function () {
                    $.Velocity(
                        el,
                        { opacity: 0 },
                        { complete: done }
                    )
                }, delay)
            }
        },
        components: {
            appQuestion: Question
        },
        created() {
            this.getExam();           
        },
        deactivated () {
            clearInterval(this.time);
        }
    }
</script>



