import Vue from 'vue'
import App from './App.vue'
import sweetAlert from 'sweetalert'
import VeeValidate from 'vee-validate'

const config = {
  locale: 'tw', 
  dictionary: {
    tw: {
      messages:{
        email: () => '請填寫正確E-mail格式',
        required: () => '此欄位必填',
        numeric: () => '此欄位只能填寫數字',
        min: ()=> '請輸入至少8個字元',
        max: ()=> '最多18個字元',
        confirmed: ()=> '與設定密碼不符',
        twID: ()=> '格式錯誤',
        twPhone: ()=> '手機格式錯誤',
        plateNo: ()=> '車牌格式錯誤'
      }
    }
  }, 
};
VeeValidate.Validator.extend('twPhone', {
  getMessage: (field) => 'The ' +field + ' value is not truthy.',
  validate: phoneNumber => {
    phoneNumber = phoneNumber.toString();
    var cellphone = /^[09]{2}[0-9]{8}$/; 
    if ( cellphone.test( phoneNumber ) ) {
      return true; 
    }else{return false;} 
  }
});
Vue.use(VeeValidate, config);

new Vue({
  el: '#app',
  render: h => h(App)
})



